<template>
  <div>
    <b-modal okVariant="danger" size="lg" id="modalPopover" title="Error log" scrollable ok-only>
      <p v-for="(error, index) in errors" :key="index">{{error}}</p>
    </b-modal>
    <b-input-group style="max-width: 50rem; margin: 0 auto; height: 3rem;" class="mb-5">
      <b-form-input
        aria-describedby="input-live-help input-live-feedback"
        :state="nameState"
        size="lg"
        class="mr-sm-2"
        placeholder="Hae paikkakuntaa"
        v-model="text"
        @keydown.enter.prevent="getWeather(text)"
        @click="clearState"
      ></b-form-input>
      <b-input-group-append>
        <b-button @click="getWeather(text)" size="md" variant="outline-primary">Haku</b-button>
        <b-button
          v-if="errors.length > 0"
          v-b-modal.modalPopover
          size="md"
          variant="outline-danger"
        >Error log</b-button>
      </b-input-group-append>
      <b-form-invalid-feedback id="input-live-feedback">Syötä haku</b-form-invalid-feedback>
    </b-input-group>
    <div style="margin: 0 auto; max-width: 100rem;" class="mt-1">
      <b-row>
        <b-col v-if="weather.name" lg>
          <b-container style="max-width: 40rem">
            <H4>Säätiedot nyt:</H4>
            <b-card class="mb-3" header-tag="header">
              <template v-slot:header>
                <b-row>
                  <b-col>
                    <div>
                      <h5 class="mt-3">{{weather.name}}</h5>
                    </div>
                  </b-col>
                  <b-col>
                    <div>
                      <b-img
                        :src="'http://openweathermap.org/img/w/' + weather.weather[0].icon + '.png' "
                        blank-color="#ccc"
                        fluid
                      ></b-img>
                    </div>
                  </b-col>
                </b-row>
              </template>
              <b-media>
                <b-row>
                  <b-col>
                    <div>Lämpötila: {{weather.main.temp}}c</div>
                    <div>Tuntuu kuin: {{weather.main.feels_like}}c</div>
                    <div>Minimi: {{weather.main.temp_min}}c</div>
                    <div>Maksimi: {{weather.main.temp_max}}c</div>
                    <div>Tuulen nopeus: {{weather.wind.speed}}m/s</div>
                  </b-col>
                  <b-col>
                    <div>Säätila: {{weather.weather[0].description}}</div>
                    <div>Pilvisyys: {{weather.clouds.all}}%</div>
                    <div>Aurinko nousee: {{weather.sys.sunrise | moment("HH:mm")}}</div>
                    <div>Aurinko laskee: {{weather.sys.sunset | moment("HH:mm")}}</div>
                  </b-col>
                </b-row>
              </b-media>
            </b-card>
          </b-container>
        </b-col>
        <b-col v-if="forecast.list" lg>
          <b-container style="max-width: 40rem">
            <H4>Ennuste:</H4>
            <div style="overflow-y: scroll; height: 40rem;">
              <b-card
                class="mb-4"
                header-tag="header"
                v-for="(forecast, index) in forecast.list"
                :key="index"
              >
                <template v-slot:header>
                  <b-row>
                    <b-col>
                      <div>
                        <h5 class="mt-3">{{forecast.dt | moment("DD.MM.YYYY, HH:mm")}}</h5>
                      </div>
                    </b-col>
                    <b-col>
                      <b-container fluid>
                        <b-img
                          :src="'http://openweathermap.org/img/w/' + forecast.weather[0].icon + '.png' "
                          blank-color="#ccc"
                          fluid
                        ></b-img>
                      </b-container>
                    </b-col>
                  </b-row>
                </template>
                <b-media>
                  <b-row>
                    <b-col>
                      <div>Lämpötila: {{forecast.main.temp}}c</div>
                      <div>Tuntuu kuin: {{forecast.main.feels_like}}c</div>
                      <div>Minimi: {{forecast.main.temp_min}}c</div>
                      <div>Maksimi: {{forecast.main.temp_max}}c</div>
                    </b-col>
                    <b-col>
                      <div>Säätila: {{forecast.weather[0].description}}</div>
                      <div>Pilvisyys: {{forecast.clouds.all}}%</div>
                      <div>Tuulen nopeus: {{forecast.wind.speed}}m/s</div>
                    </b-col>
                  </b-row>
                </b-media>
              </b-card>
            </div>
          </b-container>
        </b-col>
      </b-row>
    </div>
  </div>
</template>
<script>
import axios from "@/plugins/axios"
import moment from 'moment'

export default {
  name: 'Weather',
  data () {
    return {
      nameState: null,
      text: '',
      weather: [],
      forecast: [],
      errors: [],
    }
  },
  methods: {
    getWeather (city) {
      if ((!/^\s/.test(city)) && city != '') {
        city = city.replace(/\s+$/, '')
        Promise.all([
          axios.getResponse("weather", city),
          axios.getResponse("forecast", city)
        ]).then(([weather, forecast]) => {
          // eslint-disable-next-line
          console.log(weather)
          if (weather.data.cod == "200") {
            this.forecast = forecast.data
            this.weather = weather.data
          } else {
            this.errorHandler(weather)
          }
        })
          .catch(err => {
            this.errors.push(moment(new Date()).format('DD.MM.YYYY, HH:mm') + " " + err.toString())
            this.showError(err.toString())
          })
      } else {
        this.nameState = false
      }
      this.text = ''
    },
    showError (error) {
      this.$bvModal.msgBoxOk(error, {
        id: 'alert',
        title: '',
        size: 'lg',
        okTitle: 'Ok',
        okVariant: 'danger',
        headerClass: 'p-2 border-bottom-0',
        footerClass: 'p-2 border-top-0',
      })
      setTimeout(() => { this.$bvModal.hide('alert') }, 5000)
    },
    errorHandler (error) {
      this.errors.push(moment(new Date()).format('DD.MM.YYYY, HH:mm') + " Error: " + error.data.cod + ", " + error.data.message + " from " + error.config.url)
      this.showError("Kaupunkia ei löytynyt!")
    },
    clearState () {
      this.nameState = null
    }
  },
  mounted () {
    this.getWeather("Helsinki")
  }
}
</script>
<style scoped>
</style>